import './ControlPanelStyle.css';
import Dashboard from './pages/Dashboard';
import ReactDOM from 'react-dom';
import DataTable from './components/employees-table';
import NewUser from './pages/NewUser';
import {BiLogOut} from "react-icons/bi";
import React, { useState, useEffect } from 'react';
import Home from '../index';


const NavBar = () => {
  const [seconds, setSeconds] = useState(599);
  var [m, setM] = useState("10");
  var [s, setS] = useState("00");

  function reset() {
    setSeconds(600);
  }
  useEffect(() => {
    var sc = "";
    let interval = null;
    interval = setInterval(() => {
      setSeconds(seconds => seconds - 1);
      setM(m => m = Math.floor(seconds / 60));
      if((seconds % 60) < 10){
        sc = "0" + (seconds % 60)
      }
      else {sc = "" + (seconds % 60)}
      setS(s => s = sc);
    }, 1000)
    if (seconds === 0) {
      clearInterval(interval);
    }
    return () => clearInterval(interval);
  }, [seconds]);

  return (
    <div>
      <div className="info">
        <div className="cred-row"><div id="admin-name" className="user-credentials">Vardas Pavarde</div><BiLogOut onClick={() => Home()} className="logout-logo" /></div>
        <div id="admin-role" className="user-role">Rolė</div>
      </div>
      <button className="page-buttons" onClick={() => ReactDOM.render(<Dashboard />, document.getElementById('panelroot'))}>
        Pradinis
      </button>
      <button className="page-buttons" onClick={() => ReactDOM.render(<DataTable />, document.getElementById('panelroot'))}>
        Visi vartotojai
      </button>
      <button className="page-buttons" onClick={() => ReactDOM.render(<NewUser />, document.getElementById('panelroot'))}>
        Pridėti naują vartotoją
      </button>
      <br/>
      <div className="row">
        <div id="tim">Sesijos laikas: {m}:{s}</div>
        <div className="session"><button className="menubutton" onClick={reset}>Atnaujinti sesija</button></div>
      </div>
    </div>

  );
}

export default NavBar;