import '../ControlPanelStyle.css';
import {newusr} from '../get-user';

const NewUser = () => {
  return (
    <div className="dashboard-page">
      <div><h2>Pridėti naują vartotoją</h2></div>
      <div className="add-col">
          <input className="input-add" type="text" id="new-name" placeholder="Vardas"/>
          <input className="input-add" type="text" id="new-surname" placeholder="Pavardė"/>
          <input className="input-add" type="number" id="new-days" placeholder="Laisvos dienos"/>
          <input className="input-add" type="number" id="new-code" placeholder="Asmens kodas"/>
          <div id="add-msg"></div>
          <button className="page-buttons" onClick={() => newusr()}>Pridėti</button>
      </div>
    </div>
  );
};

export default NewUser;