import firebase from 'firebase';
import '../ControlPanelStyle.css';

var Duid;
var Uuid;
var DelTableData;
var UpdTableData;
var Data = "";
export function TableActionDel(delindex) {

  return (
    DelTableData = firebase.database().ref("User").orderByChild("id").equalTo(delindex + 1),
    DelTableData.once('value', (snapshot) => {
        snapshot.forEach((childSnapshot) => {
          Data = childSnapshot.val();
          Duid = childSnapshot.key;
          
        });
        var delUser = firebase.database().ref("User/" + Duid);
        delUser.update({
          status: "1"
        }); 
    })
  );
}
export function TableActionUpd(updindex, name, surname, days) {

  return (
    UpdTableData = firebase.database().ref("User").orderByChild("id").equalTo(updindex + 1),
    UpdTableData.once('value', (snapshot) => {
        snapshot.forEach((childSnapshot) => {
          Data = childSnapshot.val();
          Uuid = childSnapshot.key;
          
        });
        var updUser = firebase.database().ref("User/" + Uuid);
        updUser.update({
          name: name,
          surname: surname,
          days: days
        }); 
    })
  );
}