import firebase from 'firebase';
import {Crl} from './ControlPanel';

var EmployeesLogin;
var kodas;
var slaptazodis;
var Data;
export function getLogin() {
    return(
        kodas = document.getElementById('code').value,
        slaptazodis = document.getElementById('password').value,
        EmployeesLogin = firebase.database().ref("Admin").orderByChild("code").equalTo(kodas),
        EmployeesLogin.once('value', (snapshot) => {
            snapshot.forEach((childSnapshot) => {
                Data = childSnapshot.val();
            });   
            if((kodas === Data.code)&&(slaptazodis === Data.password)){
                document.getElementById("ErrorMsg").innerHTML = "";
                Crl()
                document.getElementById("admin-name").innerHTML = Data.name + " " + Data.surname;
                if(Data.permissions === "1"){
                    document.getElementById("admin-role").innerHTML = "Administratorius"
                }
                if(Data.permissions === "2"){
                    document.getElementById("admin-role").innerHTML = "Moderatorius"
                }
            }
            else document.getElementById("ErrorMsg").innerHTML = "Blogi prisijungimo duomenys";
        })
    );  
};