import './ControlPanelStyle.css';
import NavBar from './NavBar';
import SideBar from './SideBar';
import ReactDOM from 'react-dom';
import { BiLeftArrowAlt } from "react-icons/bi";
import { BiRightArrowAlt } from "react-icons/bi";

export const Crl = () =>{
  return(
    <div/>,
    ReactDOM.render(null, document.getElementById('header')),
    ReactDOM.render(null, document.getElementById('footer')),
    ReactDOM.render(<ControlPanel />, document.getElementById('root'))
  )
}

const ControlPanel = () => {
  return (
    <div className="panel-page">
      <BiLeftArrowAlt id="collapse" onClick={() => Collapse()} className="collapse"/>
      <BiRightArrowAlt id="expand" onClick={() => Expand()} className="expand"/>
      <div id="navbar" className="navbar">
        <NavBar />
      </div>
      <div id="sidebar" className="sidebar">
        <SideBar />
      </div>
    </div>
  );
};

function Collapse() {
  return (
    document.getElementById("navbar").style.display = "none",
    document.getElementById("collapse").style.display = "none",
    document.getElementById("expand").style.display = "block",
    document.getElementById("sidebar").style.width = "100%"
  );
};

function Expand() {
  return (
    document.getElementById("navbar").style.display = "block",
    document.getElementById("collapse").style.display = "block",
    document.getElementById("expand").style.display = "none",
    document.getElementById("sidebar").style.width = "calc(100% - 303px)"
  );
};

export default ControlPanel;