import './ControlPanelStyle.css';
import ReactDOM from 'react-dom';
import Login from '../Login';
import Register from '../Register';
import LoginAdmins from './LoginAdmins';
import '../Firebase';

function Start() {
  return (
    <div className="container page-start">
      <div className="small-container">
        <div id="user" className="content">
          <div onClick={() => ReactDOM.render(<LoginAdmins />, document.getElementById('root'))}><h2>Sveiki, atvykę!</h2></div>
          <div>
            <button className="page-buttons" onClick={() => ReactDOM.render(<Login />, document.getElementById('root'))}>Prisijungimas</button>
          </div>
          <div>
            <button className="page-buttons" onClick={() => ReactDOM.render(<Register />, document.getElementById('root'))}>Registracija</button>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Start;