import firebase from 'firebase';
import './ControlPanelStyle.css';

var EmployeesAdd;
var Dblength;
var Data;
var Data2;
var vardas;
var pavarde;
var dienos;
var kodas;
var Dbl;
export function newusr(){
    return(
        Dblength = firebase.database().ref("User"),
        Dblength.once('value', (snapshot) => {
            snapshot.forEach((childSnapshot) => {
                Data = childSnapshot.val();
            });
            Dbl = Data.id;
        }),
        vardas = document.getElementById('new-name').value,
        pavarde = document.getElementById('new-surname').value,
        dienos = document.getElementById('new-days').value,
        kodas = document.getElementById('new-code').value,
        EmployeesAdd = firebase.database().ref("User").orderByChild("code").equalTo(kodas),
        EmployeesAdd.once('value', (snapshot) => {
            snapshot.forEach((childSnapshot) => {
                Data2 = childSnapshot.val();
            })
            if((Data2 === undefined)){
                var formUser = firebase.database().ref("User");
                var newUser = formUser.push();
                newUser.set({
                    id: (Dbl + 1),
                    name: vardas,
                    surname: pavarde,
                    days: dienos,
                    code: kodas,
                    password: "",
                    status: "0"
                })
                document.getElementById('new-name').value = "";
                document.getElementById('new-surname').value = "";
                document.getElementById('new-days').value = "";
                document.getElementById('new-code').value = "";
                document.getElementById('add-msg').innerHTML = "Vartotojas pridėtas";
                Dblength.off();
                EmployeesAdd.off();
            }
            else{
                document.getElementById('new-name').value = "";
                document.getElementById('new-surname').value = "";
                document.getElementById('new-days').value = "";
                document.getElementById('new-code').value = "";
                document.getElementById('add-msg').innerHTML = "Vartotojas nepridėtas";
                Dblength.off();
                EmployeesAdd.off();
            }
        })
    );
}